'use client';

import { Button } from "@/components/ui/button";
import { AdvancedLessonVideo } from "@/components/video-player";
import { PlayCircle, ChevronDown } from "lucide-react";
import { getFile, parseOptions } from "@/lib/utils";
import { Collapsible, CollapsibleContent, CollapsibleTrigger } from "@/components/ui/collapsible";
import { RadioGroup, RadioGroupItem } from "@/components/ui/radio-group";
import { Label } from "@/components/ui/label";
import { Dialog, DialogContent, DialogHeader, DialogTitle } from "@/components/ui/dialog";
import { useState } from "react";
import { toast } from "sonner";
import { quiz } from "./sections/CourseSection";
import { Card, CardContent } from "@/components/ui/card";
import { TabsContent } from "@/components/ui/tabs";
import { Course } from "./sections/CourseSection";

export default function Curriculum({ course }: { course: Course | null }) {

    const [selectedAnswers, setSelectedAnswers] = useState<Record<string, string>>({});
    const [quizResults, setQuizResults] = useState<Record<string, boolean>>({});
    const [playingVideo, setPlayingVideo] = useState<string | null>(null);
    const [openQuizLesson, setOpenQuizLesson] = useState<string | null>(null);
    const [completedLessons, setCompletedLessons] = useState<string[]>(["lesson-1-1"]);

    const handleQuizSubmit = (lessonId: string, quiz?: quiz) => {
        const userAnswer = selectedAnswers[lessonId];
    };

    const markLessonCompleted = (lessonId: string) => {
        setCompletedLessons((prev) => (prev.includes(lessonId) ? prev : [...prev, lessonId]));
        toast.success("Lesson marked as completed");
    };

    return (
        <TabsContent value="curriculum" className="mt-8">
            <Card>
                <CardContent className="p-6">
                    <h2 className="text-2xl font-bold mb-6">Course Curriculum</h2>
                    <div className="space-y-4">

                        {/*  ONLY ONE LOOP */}
                        {course?.lessions?.map((lesson, lessonIndex) => (
                            <Collapsible key={lesson.id}>
                                <div className="border rounded">

                                    {/* LESSON HEADER */}
                                    <CollapsibleTrigger className="w-full p-4 flex justify-between items-center hover:bg-muted/30">
                                        <div className="flex items-center gap-3">
                                            <PlayCircle className="h-5 w-5 text-primary" />
                                            <p className="font-medium text-foreground">
                                                {lessonIndex + 1}. {lesson.title}
                                            </p>
                                        </div>
                                        <ChevronDown className="h-4 w-4 text-muted-foreground" />
                                    </CollapsibleTrigger>

                                    {/* LESSON CONTENT */}
                                    <CollapsibleContent>
                                        <div className="p-4 space-y-4 bg-muted/20">

                                            {/* DESCRIPTION */}
                                            <p className="text-sm text-muted-foreground">
                                                {lesson.description}
                                            </p>

                                            {/* VIDEO */}
                                            <AdvancedLessonVideo
                                                src={getFile(lesson.files[0].file_path)}
                                                lessonId={lesson.id.toString()}
                                                title={lesson.title}
                                                onComplete={markLessonCompleted}
                                                playingVideo={playingVideo}
                                                setPlayingVideo={setPlayingVideo}
                                            />

                                            {/* QUIZ */}
                                            {lesson.quiz && (
                                                <div>
                                                    <Button
                                                        variant="outline"
                                                        className="w-full justify-between"
                                                        onClick={() => setOpenQuizLesson(lesson.id.toString())}
                                                    >
                                                        <span>Open lesson quiz</span>
                                                        <ChevronDown className="h-4 w-4" />
                                                    </Button>

                                                    <Dialog
                                                        open={openQuizLesson === lesson.id.toString()}
                                                        onOpenChange={(open) =>
                                                            !open && setOpenQuizLesson(null)
                                                        }
                                                    >
                                                        <DialogContent className="max-w-lg">
                                                            <DialogHeader>
                                                                <DialogTitle>{lesson.quiz.title}</DialogTitle>
                                                            </DialogHeader>

                                                            <div className="mt-4 space-y-5">
                                                                {lesson.quiz.questions?.map((q, qIndex) => (
                                                                    <div key={q.id}>
                                                                        <p className="font-medium mb-2">
                                                                            {qIndex + 1}. {q.question_text}
                                                                        </p>

                                                                        <RadioGroup
                                                                            value={selectedAnswers[String(lesson.id)]}
                                                                            onValueChange={(value) =>
                                                                                setSelectedAnswers(prev => ({
                                                                                    ...prev,
                                                                                    [lesson.id]: value,
                                                                                }))
                                                                            }
                                                                        >
                                                                            {parseOptions(q.options).map((opt, idx) => (
                                                                                <div key={idx} className="flex items-center space-x-2">
                                                                                    <RadioGroupItem
                                                                                        id={`${lesson.id}-${idx}`}
                                                                                        value={opt}
                                                                                    />
                                                                                    <Label htmlFor={`${lesson.id}-${idx}`}>
                                                                                        {opt}
                                                                                    </Label>
                                                                                </div>
                                                                            ))}
                                                                        </RadioGroup>
                                                                    </div>
                                                                ))}

                                                                <Button
                                                                    className="w-full"
                                                                    disabled={!selectedAnswers[String(lesson.id)]}
                                                                    onClick={() => handleQuizSubmit(String(lesson.id), lesson.quiz)}
                                                                >
                                                                    Submit Answer
                                                                </Button>
                                                            </div>
                                                        </DialogContent>
                                                    </Dialog>
                                                </div>
                                            )}

                                        </div>
                                    </CollapsibleContent>

                                </div>
                            </Collapsible>
                        ))}

                        {/* END SINGLE LOOP */}

                    </div>
                </CardContent>
            </Card>
        </TabsContent>
    );
}
