import { Button } from "@/components/ui/button";
import { Slider } from "@/components/ui/slider";
import {Play, Pause, Volume2, VolumeX } from "lucide-react";
import { useState, useRef } from "react";

type AdvancedLessonVideoProps = {
    src: string;
    lessonId: string;
    title: string;
    onComplete: (lessonId: string) => void;
    playingVideo: string | null;
    setPlayingVideo: (id: string | null) => void;
};

export const AdvancedLessonVideo = ({
    src,
    lessonId,
    title,
    onComplete,
    playingVideo,
    setPlayingVideo,
}: AdvancedLessonVideoProps) => {
    const videoRef = useRef<HTMLVideoElement | null>(null);
    const [duration, setDuration] = useState(0);
    const [currentTime, setCurrentTime] = useState(0);
    const [volume, setVolume] = useState(1);
    const [isMuted, setIsMuted] = useState(false);
    const [playbackRate, setPlaybackRate] = useState(1);

    const isPlaying = playingVideo === lessonId;

    const togglePlay = () => {
        const el = videoRef.current;
        if (!el) return;
        if (isPlaying) {
            el.pause();
            setPlayingVideo(null);
        } else {
            el.play();
            setPlayingVideo(lessonId);
        }
    };

    const handleTimeUpdate = () => {
        const el = videoRef.current;
        if (!el) return;
        setCurrentTime(el.currentTime);
        setDuration(el.duration || 0);
    };

    const handleSeek = (value: number[]) => {
        const el = videoRef.current;
        if (!el) return;
        const nextTime = value[0];
        el.currentTime = nextTime;
        setCurrentTime(nextTime);
    };

    const handleVolumeChange = (value: number[]) => {
        const el = videoRef.current;
        if (!el) return;
        const nextVolume = value[0];
        el.volume = nextVolume;
        setVolume(nextVolume);
        setIsMuted(nextVolume === 0);
    };

    const toggleMute = () => {
        const el = videoRef.current;
        if (!el) return;
        const nextMuted = !isMuted;
        el.muted = nextMuted;
        setIsMuted(nextMuted);
    };

    const handleRateChange = (rate: number) => {
        const el = videoRef.current;
        if (!el) return;
        el.playbackRate = rate;
        setPlaybackRate(rate);
    };

    const handleEnded = () => {
        setPlayingVideo(null);
        onComplete(lessonId);
    };

    const formatTime = (time: number) => {
        if (!Number.isFinite(time)) return "0:00";
        const minutes = Math.floor(time / 60);
        const seconds = Math.floor(time % 60)
            .toString()
            .padStart(2, "0");
        return `${minutes}:${seconds}`;
    };

    const progress = duration ? (currentTime / duration) * 100 : 0;

    return (
        <div className="relative bg-black rounded-lg overflow-hidden">
            <video
                ref={videoRef}
                className="w-full aspect-video"
                controls={false}
                onTimeUpdate={handleTimeUpdate}
                onLoadedMetadata={handleTimeUpdate}
                onEnded={handleEnded}
            >
                <source src={src} type="video/mp4" />
                Your browser does not support the video tag.
            </video>
            <div className="absolute inset-x-0 bottom-0 bg-gradient-to-t from-black/80 to-transparent p-4 space-y-2">
                <div className="text-xs text-white/80 mb-1 truncate">{title}</div>
                <div className="flex items-center gap-3 text-white">
                    <Button
                        variant="ghost"
                        size="icon"
                        className="h-8 w-8 text-white hover:bg-white/20"
                        onClick={togglePlay}
                    >
                        {isPlaying ? <Pause className="h-4 w-4" /> : <Play className="h-4 w-4" />}
                    </Button>
                    <div className="flex-1 flex items-center gap-3">
                        <div className="flex items-center gap-2 text-xs min-w-[72px]">
                            <span>{formatTime(currentTime)}</span>
                            <span className="text-white/60">/</span>
                            <span>{formatTime(duration)}</span>
                        </div>
                        <div className="flex-1">
                            <Slider
                                value={[currentTime]}
                                max={duration || 0}
                                step={0.1}
                                onValueChange={handleSeek}
                            />
                        </div>
                    </div>
                    <div className="flex items-center gap-2">
                        <Button
                            variant="ghost"
                            size="icon"
                            className="h-8 w-8 text-white hover:bg-white/20"
                            onClick={toggleMute}
                        >
                            {isMuted || volume === 0 ? (
                                <VolumeX className="h-4 w-4" />
                            ) : (
                                <Volume2 className="h-4 w-4" />
                            )}
                        </Button>
                        <div className="w-20">
                            <Slider
                                value={[volume]}
                                max={1}
                                step={0.05}
                                onValueChange={handleVolumeChange}
                            />
                        </div>
                        <select
                            className="bg-black/40 text-xs text-white px-2 py-1 rounded-md border border-white/10"
                            value={playbackRate}
                            onChange={(e) => handleRateChange(Number(e.target.value))}
                        >
                            {[0.5, 1, 1.5, 2].map((rate) => (
                                <option key={rate} value={rate}>
                                    {rate}x
                                </option>
                            ))}
                        </select>
                    </div>
                </div>
            </div>
        </div>
    );
};